.container{
  display: flex;
  /* align-items: center; */
  justify-content: center;
  margin-top: 100px;
  /* height: 100%; */
}

.deck{
  position: absolute;
  /* width: 300px;
  height: 700px; */
  /* margin-left: 50%;
  margin-right: 50%; */
  will-change: transform;
  display: flex;
  align-items: center;
  justify-content: center; 
  touch-action: none; 
} 
  
.deck > div {
  background-color: transparent;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center center;
  width: 60vw;
  max-width: 400px;
  height: 70vh;
  max-height: 700px;
  will-change: transform;
  border-radius: 1px;
  box-shadow: 0 12.5px 100px -10px rgba(50, 50, 73, 0.4), 0 10px 10px -10px rgba(50, 50, 73, 0.3);
}

